import * as express from 'express';
import PlatformDataSource from '../../database/datasource/PlatformDataSource';
import { ContainerRegistry } from '../../nodes/container';
import OsUtils from '../../nodes/utils/os-utils';

let router = express.Router();

// get platform data
router.get('', async function (req, res) {
  try {
    const platform = await PlatformDataSource.getPlatform();
    res.json(platform);
  } catch (e) {
    res.status(500).send(e);
  }
});

// update platform
router.put('', async function ({ body }, res) {
  try {
    await PlatformDataSource.updatePlatform(body);
    res.status(201).send("successful");
  } catch (e) {
    res.status(500).send(e);
  }
});

//download nodes
router.get('/wires/download/nodes', async function (req, res) {
  try {
    const container = ContainerRegistry.instance().rootContainer();
    container.db.getNodes((err, docs) => {
      if (!err) {
        res.json(JSON.stringify(docs));
      } else {
        res.status(500);
      }
    });
  } catch (e) {
    res.status(500).send(e);
  }
});

//get disc space
router.get('/host/info/disc', async function (req, res) {
  try {
    OsUtils.fsSize()
      .then(obj => {
        res.json(JSON.stringify(obj));
      })
      .catch(err => {
        res.status(500).send(err);
      });
  } catch (e) {
    res.status(500).send(e);
  }
});

module.exports = router;

